import kitchen from "../../../assets/kitchen.jpg"

const OurStory =  () => {
    return (
        <div className="slices-container" id="our-story">
            <div className="slice">
                <div>
                    <h3>Nuestra historia</h3>
                    <p>Appetitoso es una osteria-pizzera contempornea en el corazn de San Pedro Garza Garca, Mxico. Trabajamos con los mejores productos locales e italianos para ofrecer las autnticas tradiciones culinarias de Italia.</p>
                    <p>Todos nuestros platos se elaboran en el lugar con ingredientes de temporada de calidad y se sirven junto con una seleccin de cervezas artesanales italianas, vinos y ccteles tradicionales.</p>
                </div>
            </div>
            <div className="slice">
                <img className="img-cover" src={kitchen} alt=""/>
            </div>
        </div>
    ) 
}
        
export default OurStory